.wrapper {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  display: flex;
  flex-wrap: wrap;
  height: 862px;
  width: 944px;
  padding: 50px 0;

  &__card {
    margin-right: 48px;
    margin-bottom: 46px;
  }

  &:nth-last-child(-n + 4) {
    margin-bottom: 0;
  }

  &__card:nth-child(4n) {
    margin-right: 0;
  }
}
